.o-card__image {
  box-sizing: inherit;
  display: block;
  margin: 0 0 10px;
  min-width: 0;
  flex-shrink: 0;
  order: 1; }
  .o-card__image--top {
    order: -1;
    margin: 0 0 10px; }
  .o-card__image--right {
    float: right;
    width: 50%;
    padding-left: 10px;
    margin: 0; }
  .o-card__image--bottom {
    order: 6;
    margin: 10px 0 0; }
  .o-card__image--left {
    float: left;
    width: 40%;
    padding-right: 10px;
    margin: 0; }
  .o-card__image img {
    display: block;
    max-width: 100%;
    height: auto;
    margin: 0; }

.o-card__related-content {
  color: #505050;
  list-style: none inside;
  margin: 5px 0 10px;
  padding: 0;
  order: 6; }

.o-card__related-content-item {
  font-family: MetricWeb, sans-serif;
  line-height: 17px;
  font-size: 15px;
  font-weight: 400;
  color: #333333;
  padding: 2px 0; }
  .o-card__related-content-item:before {
    content: '>';
    display: inline-block;
    margin-right: 10px; }
  .o-card__related-content-item a {
    color: inherit;
    text-decoration: none; }
  .o-card__related-content-item a:hover {
    border-bottom: 1px dotted; }

.text-center {
  text-align: center; }

.mb-2 {
  margin-bottom: 0.5rem; }

.mb-4 {
  margin-bottom: 2rem; }

.nw-header {
  color: #ffffff; }
  .nw-header:hover {
    color: #eeeeee; }

.nw-page-count {
  color: #26747a;
  font-style: italic;
  margin-bottom: 0; }

html {
  background-color: #fff1e0;
  font-family: BentonSans, sans-serif;
  overflow-x: hidden; }

body {
  margin: 0; }
